body {
  font-family: arial, helvetica, sans-serif;
  background: rgb(19,117,109);
  margin: 0
}

.flexRow {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-between;
}

.header {
  color: white;
  background: rgb(250,80,70);
  padding: 10px 200px 15px 200px;
  text-align: center;
}
.title {
  margin: 0 0 10px 0;
  font-size: 1.5em;
}
.newTask {
  box-sizing: border-box;
  width: 100%;
  height: 30px;
  border: none;
  margin-bottom: 15px;
  font-size: 0.8em;
  text-indent: 5px;
}
.addBtn {
  box-sizing: border-box;
  width: 60px;
  height: 30px;
  border: none;
  color: white;
  background: rgb(117,123,133);
  padding: 0;
  margin-bottom: 15px;
  font-size: 0.8em;
}
.select {
  width: 90px;
  height: 17px;
  border: none;
}
.search {
  width: 200px;
  height: 17px;
  border: none;
  border-radius: 5px;
  padding: 0;
  text-indent: 5px;
}

.totalTasks {
  background: rgb(0,150,136);
  padding: 20px 0;
  margin: 0;
  font-size: 1.5em;
  text-align: center;
}
.emphasis {
  font-size: 2em;
}

ul {
  margin: 0;
  padding: 0;
}
li {
/*  list-style: none; */
  font-size: 1.1em;
  padding: 15px 25px 15px 5px ;
  background: -webkit-linear-gradient(rgb(0,215,200) 0%, rgb(0,190,180) 100%);
  background: linear-gradient(rgb(0,215,200) 0%, rgb(0,190,180) 100%);
}
.taskFlexGrow {
  flex: 1 0 auto;
  padding: 0 15px;
}
button[type='submit'] {
  border: none; 
}
.taskCompleted {
  color: rgb(117,123,133);
  text-decoration: line-through;
}

.clearBtns {
  padding: 15px 200px;
}
.clearBtn {
  color: rgb(170,170,170);
  background: rgb(19,117,109);
  font-size: 1.5em;
}
.clearBtn:hover {
  color: white;
}
